.contact {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding-top: 35vh;
    padding-bottom: 25vh;
}

.contact-container {
    display: flex;
    flex: 1;
    justify-content: center;
    text-align: center;

    
}
.contact p {
    font-size: 20px;
    /* font-weight: 700; */
}

.contact-container a{
  background-color:#d3f3df;
  color: #4a0172;
}

h2 {
    font-weight: 300;
}

@media only screen and (max-width: 512px) {
    .contact {
        display: flex;
        flex-direction: column;
        flex: 1;
        padding-top: 30vh;
        padding-right: 2vw;
        padding-left: 2vw;
    } 
}


