  .home .project2 .grid-container2 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-areas:
      'roles roles desc1 desc1'
      'roles roles desc1 desc1'
      'research research research research'
      'desc2 desc2 desc2 quote'
      'desc2 desc2 desc2 quote '
      'probe probe desc3 desc3'
      'probe probe desc3 desc3'
      'desc4 desc4 zoom zoom'
      'desc4 desc4 zoom zoom'
      'desc5 desc5 desc5 desc5'
      'desc6 HMW HMW HMW'
      'desc6 HMW HMW HMW'
      'desc6_5 desc6_5 desc6_5 desc6_5'
      'desc7 desc7 desc7 desc7'
      'persona1 persona1 persona2 persona2'
      'persona1 persona1 persona2 persona2'
      'desc8 desc8 desc8 desc8'
      'journey journey journey journey'
      'journey journey journey journey'
      'desc8_5 desc8_5 desc8_5 desc8_5'
      'desc9 desc9 desc9 desc9'
      'safe1 safe2 safe3 safe4'
      'b4 b1 b2 b3'
      'refinedhmw refinedhmw refinedhmw refinedhmw'
      'prototyping prototyping prototyping prototyping'
      'desc10 desc10 pics pics2'
      'desc10 desc10 pics pics2'
      'usabilityName usabilityName usabilityName usabilityName'
      'usabilitypics usabilitypics usability usability' 
      'usability2 usability2 usability2pics usability2pics'
      'refined refined refined refined'
      'bagg bagg itemss itemss'
      'future future future future';
    gap: 1vw;
    padding: 5vh 0;
    font-family: minion, sans-serif;
    text-align: justify;
  }
  
  .home .project2 .grid-container2 > div {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
