  font-family: proxima-nova-extra-condensed, sans-serif;
  font-weight: 600;
  background-color: #273469;
  width: 100vw;
  left: calc(-50vw + 50%);  
  }

  .project9 p{
    font-weight: 300;
  }
  

  .title4 a{
    background-color:  #273469;
    color: white;
    /* letter-spacing: .5rem; */
  }
  .subtitle4 a{
