  }
  .i3_1 {
    grid-area: personatext;
  }
  .i3_2 {
    grid-area: personaphoto;
  }
  .i3_3 {
    grid-area: interviewtext
  }
  .i3_3 p {
    margin-top: 0;
    margin-bottom: 0;
  }
  .i3_4 {
    grid-area: comparisontext;
  }
  .i3_5 {
    grid-area: comparisonphoto;
  }
  .i4 {
    grid-area: maingoal;
  }
  .i4_1 {
    grid-area: hmwtext;
  }
  .i4_2 {
    grid-area: hmw;
    border-left: 5px solid #317773;
  }

  .i4_2 p{
    margin-left: 10vw;
    color: #317773; 
    font-weight: 400;
    font-size: 40px;
  }
  .i5 {
    grid-area: designprocess;
  }
  .i5_1 {
    grid-area: cardsorting;
  }
  .i5_2 {
    grid-area: cardsortingpic1;
  }
  .i5_3 {
    grid-area: cardsortingpic2;
  }
  .i5_4 {
