  .ShortDescription9 p{
    text-align: left;
    /* font-size: 12px; */
    font-family: minion, sans-serif;
    font-weight: 300;
    text-indent: 0;
  }

.ShortDescription9 {
    margin: auto;
}

.ShortDescription9 .bold {
    display: inline;
    background-color: #def4d2;
    font-weight: 500;
}

.project9 ul {
  color: black;
}

/* 'designprocess designprocess designprocess designprocess'
'cardsorting cardsorting cardsorting cardsorting'
'cardsortingpic1 cardsortingpic1 cardsortingpic2 cardsortingpic2' */

  .home .project9 .grid-container9 {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-areas:
        'roles roles maindesc maindesc'
        'roles roles maindesc maindesc'
        'highlightdesc highlightdesc highlightdesc highlightdesc'
        'result result result result'
        'personalchatgif personalchatgif personalchatgiftext personalchatgiftext'
       
        'talking talking talking talking'
        'personatext personatext personaphoto personaphoto'
        'interviewtext interviewtext interviewtext interviewtext'
        'comparisontext comparisontext comparisontext comparisonphoto'
        'hmwtext hmwtext hmwtext hmwtext'
        'hmw hmw hmw hmw'
       
        'sketchestext sketchestext sketches sketches'
        ;
      gap: 1vw;
      padding: 5vh 0;
      font-family: minion, sans-serif;
      text-align: justify;
  }
    
  .home .project9 .grid-container9 > div {
      background-color: rgba(255, 255, 255, 0.8);
      text-align: center;
      padding: 20px 0;
      font-size: 15px;
      font-weight: 300;
      text-align: justify;
  }

  .project9 .sectionName {
    color: #317773;
    font-size: 40px;
    text-align: center;
    font-family: proxima-nova-extra-condensed, sans-serif;
    /* letter-spacing: 0.2rem; */
}

