.web{
    display: flex;
    flex: 1;
    align-items: center;
}
.web-option{
    /* font-family: proxima-nova, sans-serif; */
    /* font-family: 'Raleway', sans-serif; */
    font-size: 0.9em;
    margin-right: 3vw;
    font-weight: 500;
    /* letter-spacing: .1rem; */
    
}

.web-option a{
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}

.web-logos{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

.web-logos img{
    width: 1vw;
    height: auto;
    margin-left: 1vw;
}

.option-icon{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
.web-option:last-child {
    margin-right: 0px;
}

.web-option a {
    box-shadow: inset 0 0 0 0 #D64933;
    color: black;
    margin: 0 -.25rem;
    padding: 0 .25rem;
    transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
  }
  .web-option a:hover {
    box-shadow: inset 150px 0 0 0 #D64933;
    color: white;
  }

  a{
    color: white;
  }