.WorkTab .title11 {
    
    padding: 10vh 0 0 0;
    font-size: 40px;
    text-align: center;
    font-family: proxima-nova-extra-condensed, sans-serif;
    
  }

  .title11 a{
    background-color:  white;
    color: black;
    /* letter-spacing: .5rem; */
  }
  
  .description11 {
    text-align: center;
    font-size: 15px;
    font-weight: 300;
  
  }
  
  .containerForWorks .grid-container11 {
      display: inline-grid;
      grid-template-columns: 1fr 1fr 1fr;
      background-color: white;
      
      padding: 5vh 0;
      column-gap: 1vw;
      row-gap: 10px;
      
  
    }
    
    .grid-item {
     
      /* padding: 10vh 10vw 10vh 10vw; */
      font-size: 30px;
      text-align: left;
      color: white;
      border-radius: 1px;
    }

    .gi1{
      /* background-color: #5aa0b7; */
      border: 1px solid #D64933;
    }

    .gi3{
      /* background-color: #23a8b1; */
      border: 1px solid #D64933;
    }

    .gi2{
      border: 1px solid #D64933;
    }
    .gi4{
      border: 1px solid #D64933;
    }
  
    img {
      object-fit: cover;
      width: 100%;
      margin: 0;
      border-radius: 5px;
    }

    .grid-container11 figcaption {
        color: black;
        font-size: 15px;
        font-weight: 400;
        color: white;
    }

    .gi1 figcaption {
      /* color: #5aa0b7; */
      color: black;
    }

