@font-face {
    font-family: 'Helvetica Condensed';
    src: URL('/Users/alimazhagufarova/personal/personal/front-end/public/Helvetica Medium Condensed.otf') format('truetype');
  }

.header{
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 12px;
    position: relative;
    align-items: center;
    width: 100%;
}

a {
    text-decoration: none;
}

/* .is-sticky .web-option {
    margin-left: 7vw;
} */

.menu{
    background-color: white;
    display: flex;
    flex: 1;
    justify-content: flex-end;
}


.mobile-menu{
    display: none;
}
.mobile-menu img{
    width: 25px;
    height: auto;
}
.web-menu{
    display: flex;

}

.logo{
    /* font-family: proxima-nova-extra-condensed, sans-serif; */
    font-family: Helvetica Condensed;
    display: flex;
    font-size: 33px;
    color: white;
    background-color: rgb(43, 48, 58);
    font-weight: 400;
    letter-spacing: 0.0005rem;
}

.is-sticky {
    position: relative;
    top: 0; 
    /* width: 100%; */
    z-index: 999;
    display: flex;
    /* justify-content: flex-end; */
    /* justify-content: space-between; */
    /* flex: 1; */
    /* padding: 12px; */
    /* align-items: center; */
    /* box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%); */
    background-color: #ffffff !important;
    animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
    /* padding-top: 0px;
    padding-bottom: 0px; */
    
}

.is-sticky .menu {
    /* position: relative; */
    display: flex;
    flex: 1;
    /* align-items: center; */
    /* margin-left: 28vw; */
    /* margin-right: 3vw; */
    justify-content: flex-end;
}

@media only screen and (max-width: 720px) {
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 24px;
    }
    .is-sticky{
        display: none;
    }
}

