.home .land .grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    'description room '
    'extra extra'
    'proj1 proj2'
    'proj1 proj2'
    'proj1 proj2'
  ;
  gap: 1vw;
  padding: 5vh 0;
}

.home .land .grid-container > div {
  background-color: rgba(255, 255, 255, 0);
  text-align: center;
  padding: 20px 0;
  font-size: 40px;
  font-weight: 300;
}
.home .land .grid-container {
  padding-bottom: 0;
}
