.WorkTab .title21 {
  padding: 10vh 0 0 0;
  font-size: 40px;
  text-align: center;
  font-family: proxima-nova-extra-condensed, sans-serif;
    
  }

  .title21 a{
    background-color:  white;
    color: black;
    /* letter-spacing: .5rem; */
  }
  
  .description21 {
    text-align: center;
    font-size: 15px;
    font-weight: 300;
  
  }
  .grid-item01 {
    grid-area: zeroeth;
  }
  .grid-item1 {
    grid-area: first;
  }
  .grid-item2 {
    grid-area: second;
  }
  .grid-item3 {
    grid-area: third;
  }
  /* .grid-item4 {
    grid-area: fourth;
  } */


  
  .body .grid-container21 {
      display: inline-grid;
      grid-template-columns: 1fr 1fr;
      background-color: white;
      grid-template-areas:
      'zeroeth first'
      'second third';
      padding: 5vh 0;
      column-gap: 10px;
      row-gap: 10px;
  
    }

    .grid-container21 .grid-item01 {
      background-color: rgba(255, 255, 255, 0.8);
      border: 0px solid blue;
      padding: 2vh 2vw 2vh 2vw;
      font-size: 30px;
      text-align: center;
    }
    
    .grid-container21 .grid-item1 {
      background-color: rgba(255, 255, 255, 0.8);
      border: 0px solid blue;
      padding: 2vh 2vw 2vh 2vw;
      font-size: 30px;
      text-align: center;
    }

    .grid-container21 .grid-item2 {
      background-color: rgba(255, 255, 255, 0.8);
      border: 0px solid blue;
      padding: 2vh 2vw 2vh 2vw;
      font-size: 30px;
      text-align: center;
    }
    .grid-container21 .grid-item3 {
      background-color: rgba(255, 255, 255, 0.8);
      border: 0px solid blue;
      padding: 0 2vw 2vh 2vw;
      font-size: 30px;
      text-align: center;
    }
    .grid-container21 .grid-item4 {
      background-color: rgba(255, 255, 255, 0.8);
      border: 0px solid blue;
      padding: 0 2vw 2vh 2vw;
      font-size: 30px;
      text-align: center;
    }
  
    img {
      object-fit: cover;
      width: 100%;
    }

    .grid-container21 figcaption {
        text-align: center;
        color: black;
        font-size: 10px;
        font-weight: 400;
    }

    .grid-container21 figcaption h{
        font-weight: 600;
        font-size: 20px;
    }

    .grid-item1 img:hover{
      box-shadow: 0 3px 10px rgb(0 0 0 / 1);
  }
  .grid-item01 img:hover{
    box-shadow: 0 3px 10px rgb(0 0 0 / 1);
}

  .grid-item2 img:hover{
    box-shadow: 0 3px 10px rgb(0 0 0 / 1);
}

.grid-item3 img:hover{
  box-shadow: 0 3px 10px rgb(0 0 0 / 1);
}

.grid-item4 img:hover{
  box-shadow: 0 3px 10px rgb(0 0 0 / 1);
}

@media only screen and (max-width: 512px) {
  .WorkTab .title21 {
    padding: 3vh 0 0 0;
    font-size: 40px;
    text-align: center;
    font-family: proxima-nova-extra-condensed, sans-serif;
      
    }
    .title21 a{
      background-color:  white;
      color: black;
      letter-spacing: .5rem;
    }
    
    .description21 {
      text-align: center;
      font-size: 15px;
      font-weight: 300;
    
    }
    .body .grid-container21 {
      display: inline-grid;
      grid-template-columns: 1fr;
      background-color: white;
      grid-template-areas:
      'zeroeth '
      'first'
      'second '
      'third';
      padding: 3vh 0;
      column-gap: 10px;
      row-gap: 10px;
  
    }
}
