.home .project3 .grid-container3 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      'team genDesc'
      'workinprogress workinprogress'
      'section0 section0'
      'section section'
      'prototyping prototyping'
      'wireframe desc1'
      'desc2 agendaresearch'
      'guidelines guidelines'
      'desc3 desc3'
      'prevlogo desc4'
      'newlogo desc4'
      'feedback feedback'
      'desc5 desc5'
      'responsiveness responsiveness'
      'desc6 mobver'
      
      'desc7 desc7'
      ;
    gap: 1vw;
    padding: 5vh 0;
    font-family: minion, sans-serif;
    text-align: justify;
  }

  .home .project3 .grid-container3 > div {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    /* padding: 20px 0; */
    text-align: justify;
    font-size: 15px;
    font-weight: 300;
  }

